import React, { Component as Cp } from "react";
import TaskBar from "./taskbar/TaskBar";
import Background from "./Backgound";
import meta from "../data/meta";
import WindowsManager from "./WindowsManager";
export const WMRef = React.createRef<WindowsManager>();
export default class Oinkedit extends Cp{
    render() :React.ReactNode{
        return(<>
            <TaskBar />
            <Background />
            <WindowsManager ref={WMRef} />
        </>);
    }
}
export_({WM: WMRef});
//开发环境ONLY
export function export_(arg :anyObject){
    if(meta.dev === true) for(let i in arg) (window as anyObject)[i] = arg[i];
    //else console.warn("export_ is only available under development environment.");
}