import React, { Component as Cp } from "react";
import mainStyles from "../../common-css/main.module.css";
import Logo from "../../common-components/Logo";
import TaskManager from "./TaskManager";
import CustomText from "./plugins/CustomText";
import TimeManage from "./plugins/TimeManage";
export const TMRef = React.createRef<TaskManager>();
/**@once*/
export default class Taskbar extends Cp{
    render() :React.ReactNode{
        return(
            <div className={mainStyles.noselect} style={{
                display: "flex",
                flexFlow: "row nowrap",
                height: "3rem",
                width: "97.6vw",
                boxShadow: "0 .125rem .8rem -0.125rem var(--c-black-tra)",
                zIndex: "9",
                gap: "min(3vw, 1.25rem)",
                padding: "0 1.2vw 0 1.2vw",
                alignItems: "center",
                backgroundColor: "var(--c-taskbar)",
                overflow: "hidden"
            }}>
                <Logo size="2.4rem" title="oinkedit" />
                <TaskManager ref={TMRef} />
                <div style={{
                    justifySelf: "flex-end",
                    textAlign: "right",
                    whiteSpace: "nowrap"
                }}>
                    <CustomText />
                    <TimeManage />
                </div>
            </div>
        );
    }
}