import ls from "../utils/ls";
import en_US from "./en_US";
import zh_CN from "./zh_CN";
const data = {
    en_US,
    zh_CN
}, i18n = {
    get current() :typeof zh_CN{ //zh_CN更新得比较快
        return (data as anyObject)[ls("i18n", "zh_CN")!] || data.zh_CN;
    },
    data
};
export default i18n;