import React, { Component as Cp } from "react";
import Task, { TaskInstance } from "../../window/Task";
type State = Readonly<{
    taskList :Readonly<Record<string, TaskInstance>>;
}>;
/**@once*/
export default class TaskManager extends Cp<defaultReactProps, State>{
    constructor(props :defaultReactProps){
        super(props);
        this.state = {taskList: {}};
    }
//#region CRUD API
    addTask(instance :TaskInstance, id :string){
        this.setState({
            taskList: {...this.state.taskList, [id]: instance}
        });
        //return id;
    }
    delTask(id :string){
        //在setState外获得的结果才正确，因为setState很可能是异步调用的
        const result = id in this.state.taskList;
        this.setState((state :State)=>{
            const taskList = {...state.taskList};
            if(id in taskList){
                delete taskList[id];
                //delete this.refList[id];
            }
            return{taskList};
        });
        return result;
    }
//#endregion
//#region 给任务实例调用的函数
    registerMove(id :string){
        
    }
//#endregion
    render() :React.ReactNode{
        const result = [];
        //object遍历顺序按添加的顺序，没问题的
        for(let i in this.state.taskList) result.push(this.state.taskList[i]);
        return(
            <ul style={{
                height: "100%",
                flex: "auto",
                listStyle: "none",
                display: "flex",
                flexFlow: "row nowrap",
                gap: 0,
                margin: 0,
                padding: 0
            }}>{result}</ul>
        );
    }
}