export function getPx(value :number | string, unit? :string) :number{
    if(value == 0) return 0;
    const test = document.createElement("div");
    test.style.height = typeof value == "string" ? value : `${value}${unit}`;
    test.style.position = "absolute";
    test.style.top = "-9999999px";
    test.style.left = "-99px";
    document.getElementById("oinkedit")!.append(test);
    const result = parseInt(getComputedStyle(test).height);
    test.remove();
    return result;
}
export const taskBarHW = {
    height: getPx("var(--cf-tbtaskbar)"),
    width: getPx("var(--cf-lrtaskbar)"),
}