import Dexie from "dexie";
import meta from "../data/meta";
import ls from "../utils/ls";
const l = localStorage;
const dataBase = new Dexie("oinkedit");
dataBase.version(1).stores({
    config: "key, value",
    blob: "++id"
});
dataBase.open();
export {dataBase};
export default async function iniStorage(){
    const config = dataBase.table("config");
    if(l.getItem("initialized") !== meta.version){
        console.log("ini");
        const defaultTheme = `
        --c-base: mintcream;
        --c-taskbar: beige;
        --c-menu: honeydew;
        --c-menu-1: antiquewhite;
        --c-menu-2: beige;
        --c-link: slateblue;
        --c-link-tra: #483D8B30;
        --c-link-d: darkslateblue;
        --c-active: olive;
        --c-boxshadow: skyblue;
        --c-focus: dodgerblue;
        --c-warning :#F9DF82;
        --c-danger: #FF416C;
        --c-success: #27CE7F;
        --c-text-menu: #191711;
        --c-text-main: #242322;
        --c-text-nav: black;
        --c-black-tra: #2423220F;
        --c-black-tra-1: #24232219;
        --c-black-tra-2: #24232223;
        --c-1: #CEF6E3;
        --c-2: #17F18B;
        --c-3: #FFE8CF;
        --c-3-a: #FFECCC;
        --c-4: #FDFDC4;
        --c-4-a: #F4F3CC;
        --c-5: #CCFFEC;
        --c-5-a: #C9FCE9;
        --c-6: #FFD3D3;
        --c-6-a: #FFE4E4;
        --c-black: black;
        --c-grey--2: #444;
        --c-grey--1: dimgrey;
        --c-grey: grey;
        --c-grey-1: #989898;
        --c-grey-2: darkgrey;
        --c-grey-3: #AEAEAE;
        --c-grey-4: #BABABA;
        --c-grey-4-5: #CBCBCB;
        --c-grey-5: lightgrey;
        --c-grey-6: #DADADA;
        --c-grey-7: #EAEAEA;
        --c-grey-8: #EFEFEF`.replaceAll(/\s*/g, "");
        dataBase.table("config").bulkPut([
            {key: "book-list", value: []},
            {key: "book-history", value: []},
            {key: "themes", value: {default: defaultTheme}},
            {key: "windows", value: []},
        ]);
        l.setItem("initialized", meta.version);
        l.setItem("currentBook", "");
        l.setItem("currentTheme", "default");
        //ls.setItem("lastSection", "");
        l.setItem("i18n", "zh_CN");
    }
    const theme = ls("currentTheme", "default")!, body = document.getElementById("body")!;
    body.setAttribute("style", body.getAttribute("style") || "" + (await config.get("themes")).value[theme]);
}