import React, { Component as Cp } from "react";
import Logo from "../common-components/Logo";
import mainStyles from "../common-css/main.module.css";
import VWSpace from "../common-components/VWSpace";
/**@once*/
export default class Background extends Cp<defaultReactProps>{
    render() :React.ReactNode{
        return(
            <div style={{
                height: "calc(100% - 3rem)",
                width: "100%",
                textAlign: "center",
                display: "flex",
                flexFlow: "column nowrap",
                alignItems: "center",
                color: "var(--c-grey-2)"
            }} className={`${mainStyles.noselect} ${mainStyles.nodrag}`}>
                <VWSpace height="6vh" />
                <div style={{opacity: 0.25}}><Logo size="10rem" /></div>
                <VWSpace height="3vh" />
                <div style={{}}>
                    无书籍历史
                    {/*todo:书本历史*/}
                </div>
            </div>
        );
    }
}