import React, { Component as Cp } from "react";
import mainStyles from "../../../common-css/main.module.css";
import ls from "../../../utils/ls";
import i18n from "../../../i18n";
interface State{
    editing :boolean;
}
/**@once*/
export default class CustomText extends Cp<defaultReactProps, State>{
    ref = React.createRef<HTMLDivElement>();
    constructor(props :defaultReactProps){
        super(props);
        this.state = {editing: false};
    }
    validText = ()=>{
        if(this.state.editing && this.ref.current!.innerText == "") this.ref.current!.innerHTML = "&zwj;";
    }
    keyDoneCB = (e :KeyboardEvent)=>{
        if(e.key == "Enter"){
            e.preventDefault();
            this.reset();
        }
    }
    blurCB = ()=>{
        if(this.state.editing) this.reset();
    }
    reset(){
        this.setState({editing: false});
        ls("text", this.ref.current!.innerText.replaceAll("&zwj;", "").replaceAll(" ", "&nbsp;"), true);
        document.removeEventListener("keypress", this.keyDoneCB);
    }
    onDblClick = (event :React.MouseEvent<HTMLDivElement, MouseEvent>)=>{
        if(!this.state.editing){
            this.setState({editing: true}, ()=>{
                getSelection()!.selectAllChildren(this.ref.current!);
                document.addEventListener("keypress", this.keyDoneCB);
            });
        }
    }
    render() :React.ReactNode{
        return(
            <div
                className={mainStyles.reselect}
                onDoubleClick={this.onDblClick}
                onBlur={this.blurCB}
                onInput={this.validText}
                onCompositionEnd={this.validText}
                contentEditable={this.state.editing}
                ref={this.ref}
                dangerouslySetInnerHTML={{__html: ls("text", i18n.current.taskbar.customText)!}}>
            </div>
        );
    }
}