import React, { Component as Cp, ReactElement } from "react";
import { TMRef } from "../majors/taskbar/TaskBar";
import styles from "./Task.module.css";
import Window from "./Window";
interface Props extends defaultReactProps{
    label :string;
    originRef :Window;
    id :string;
}
/**给TaskManager用的*/
export type TaskInstance = ReactElement<Props, React.JSXElementConstructor<Props>>;
type State = Readonly<{
    moving :boolean;
    focusingInWindow :boolean;
}>;
export default class Task extends Cp<Props, State>{
    constructor(props :Props){
        super(props);
        this.state = {moving: false, focusingInWindow: false};
    }
//#region 窗口回调函数
    focusInWin = ()=>this.setState({...this.state, focusingInWindow: true});
    unfocusInWin = ()=>this.setState({...this.state, focusingInWindow: false});
//#endregion
//#region 移动逻辑
    pointerMoveCB = ()=>{
        if(this.state.moving){
            console.log(this.props.label, "move");
        }
    }
    pointerUpCB = ()=>{
        if(this.state.moving){
            this.setState({...this.state, moving: false});
            document.removeEventListener("pointermove", this.pointerMoveCB);
            document.removeEventListener("pointerup", this.pointerUpCB);
            document.body.classList.remove("moving");
        }
    }
    pointerDownCB = (event :React.PointerEvent<HTMLLIElement>)=>{
        if(event.isPrimary){
            this.setState({...this.state, moving: true});
            document.body.classList.add("moving");
            document.addEventListener("pointermove", this.pointerMoveCB);
            document.addEventListener("pointerup", this.pointerUpCB);
            //todo:
            TMRef.current!.registerMove(this.props.id);
        }
    }
//#endregion
    render() :React.ReactNode{
        return(
            <li
                onPointerDown={this.pointerDownCB}
                onClick={this.props.originRef.toggleWin}
                onPointerOver={this.props.originRef.hoverInTask}
                onPointerLeave={this.props.originRef.dehoverInTask}
                className={`${styles.li}${false ? ` ${styles.fixed}` : ""}${this.state.focusingInWindow ? ` ${styles.focus}` : ""}`}
                style={{
                    cursor: this.state.moving ? "grabbing" : "grab",
                    //todo:
                }}>
                <div>{this.props.label}</div>
            </li>
        );
    }
}