import React, { Component as Cp } from "react";
import { randoma2z029 } from "../utils/general";
import Window, { WindowInstance } from "../window/Window";
type State = Readonly<{
    windowList :Readonly<Record<string, WindowInstance>>;
}>;
interface windowCons{
    label :string;
    resizable :boolean;
    top :number;
    left :number;
    width :0/*number*/;
    height :0/*number*/;
    children :Children;
}
/**@once*/
export default class WindowsManager extends Cp<defaultReactProps, State>{
    /**以备后用*/
    refList :Record<string, React.RefObject<Window>> = {};
    constructor(props :defaultReactProps){
        super(props);
        this.state = {windowList: {}};
    }
    createWin(config :windowCons) :string{
        const id = randoma2z029(15), ref = React.createRef<Window>();
        this.setState({
            windowList: {
                ...this.state.windowList,
                [id]: <Window {...config} id={id} key={id} ref={ref}>{config.children}</Window>
            }
        });
        this.refList[id] = ref;
        return id;
    }
    removeWin(id :string) :boolean{
        //在setState外获得的结果才正确，因为setState很可能是异步调用的
        const result = id in this.state.windowList;
        this.setState((state :State)=>{
            const windowList = {...state.windowList};
            if(id in windowList){
                delete windowList[id];
                delete this.refList[id];
            }
            return{windowList};
        });
        return result;
    }
    render() :React.ReactNode{
        const result = [];
        for(let id in this.state.windowList) result.push(this.state.windowList[id]);
        return(
            <div style={{
                position: "fixed"
            }}>{result}</div>
        );
    }
}