const meta = {
    //版本
    version: "0.5.2",
    //是否为开发版本，这个 flag 控制 oinkedit 的程序内显示
    dev: true,
    //是否为限时评估版本，这个 flag 仅控制是否限时评估
    isLimitedVersion: true,
    //限时，单位为秒
    limit: 120,
};
//元数据共享
Object.defineProperty(window, "oinkedit", {
    configurable: false,
    enumerable: true,
    writable: false,
    value: meta
});
export default meta;