import React, { Component as Cp, ReactElement } from "react";
import { TMRef } from "../majors/taskbar/TaskBar";
import { taskBarHW } from "../utils/css";
import Task, { TaskInstance } from "./Task";
import styles from "./Window.module.css";
export type WindowInstance = ReactElement<Props, React.JSXElementConstructor<Props>>;
interface Props extends defaultReactProps{
    id :string;
    label :string;
    resizable :boolean;
    top :number | undefined;
    left :number | undefined;
    //现在窗口仍无法调整大小，不启用这两个属性
    width :0/*number | undefined*/;
    height :0/*number | undefined*/;
    readonly children :Children;
};
type State = Readonly<{
    //状态：标题
    label :string;
    //基本状态
    minimized :boolean;
    hoveredInTask :boolean;
    fixing :boolean;
    focusingWithIn :boolean;
    active :boolean;
    moving :boolean;
    //坐标
    top :number;
    left :number;
    width :number;
    height :number;
    //贴附状态
    attached_t :boolean;
    attached_b :boolean;
    attached_l :boolean;
    attached_r :boolean;
    //配置——尽量不要运行时修改！
    config :{
        id :string;
        resizable :boolean;
    }
}>;
export default class Window extends Cp<Props, State>{
    deltaTop :number = 0;
    deltaLeft :number = 0;
    taskInstance :TaskInstance;
    taskRef = React.createRef<Task>();
    /**用于获取窗口实际高宽*/
    outerDivRef = React.createRef<HTMLDivElement>();
    constructor(props :Props){
        super(props);
        //兼容不传top或left，默认左上角
        const topLeftData = {
            top: props.top ?? taskBarHW.height,
            left: props.left ?? taskBarHW.width
        };
        this.state = {
            //默认状态
            fixing: false, active: false, focusingWithIn: false, hoveredInTask: false, minimized: false, moving: false, 
            //从props获取数据
            height: props.height, width: props.width, label: props.label,
            config: {id: props.id, resizable: props.resizable},
            //这里元素还没有渲染，所以不能获取到height和width
            //这也意味着窗口刚打开时可能会卡在奇怪的地方，但只要拖动一下就会立刻变正常
            //目前先用着props的height和width，未启用，所以没啥问题
            ...this.checkPos(topLeftData, props.height, props.width),
            //先check再传数据，这样才能拿到check过的坐标
            ...topLeftData
        };
        this.taskInstance = <Task id={props.id} label={props.label} originRef={this} key={props.id} ref={this.taskRef} />;
        TMRef.current!.addTask(this.taskInstance, props.id);
    }
//#region 生命周期函数
    /**在WM中调用removeWin就会触发这个，我们在这里把任务实例扔了*/
    componentWillUnmount() :void{
        TMRef.current!.delTask(this.state.config.id);
    }
//#endregion
//#region 任务栏回调函数
    toggleWin = ()=>this.setState({...this.state, minimized: !this.state.minimized});
    hoverInTask = ()=>this.setState({...this.state, hoveredInTask: true});
    dehoverInTask = ()=>this.setState({...this.state, hoveredInTask: false});
//#endregion
//#region 任务栏启发函数，因为刚创建实例时任务实例仍未创建，ref是空的
    focusInWin_Booter = ()=>this.taskRef.current!.focusInWin();
    unfocusInWin_Booter = ()=>this.taskRef.current!.unfocusInWin();
//#endregion
//#region 窗口移动工具方法
    /**测试一组坐标是否超出浏览器窗口界限，如果超出则拉回至合法坐标
     * @weird 怪异函数：通过修改引用类型入参返回了其他的信息。*/
    checkPos(topLeftData :{top :number, left :number}, height :number, width :number){
        var attached_t = false, attached_b = false ,attached_l = false, attached_r = false;
        const b = document.body;
        if(topLeftData.top >= b.clientHeight - height){
            attached_b = true;
            topLeftData.top = b.clientHeight - height;
        }
        if(topLeftData.left >= b.clientWidth - width - 1){ //-1是为了不让文本换行
            attached_r = true;
            topLeftData.left = b.clientWidth - width - 1;
        }
        if(topLeftData.top <= taskBarHW.height){
            attached_t = true;
            topLeftData.top = taskBarHW.height;
        }
        if(topLeftData.left <= taskBarHW.width){
            attached_l = true;
            topLeftData.left = taskBarHW.width;
        }
        return {attached_t, attached_b, attached_l, attached_r};
    }
    /**获取窗口实时宽高*/
    getHW(){
        const styles = getComputedStyle(this.outerDivRef.current!),
              height = parseFloat(styles.height) + parseFloat(styles.paddingTop) + parseFloat(styles.paddingBottom),
              width = parseFloat(styles.width) + parseFloat(styles.paddingLeft) + parseFloat(styles.paddingLeft);
        return {height, width};
    }
//#endregion
//#region 窗口移动逻辑
    pointerMoveCB = (event :PointerEvent)=>{
        if(this.state.moving) this.setState((state :State)=>{
            const {height, width} = this.getHW(),
                  //计算坐标
                  topLeftData = {top: event.clientY - this.deltaTop, left: event.clientX - this.deltaLeft},
                  //检查坐标
                  attacheds = this.checkPos(topLeftData, height, width);
            return {...state, ...attacheds, top: topLeftData.top, left: topLeftData.left};
        });
    }
    pointerUpCB = ()=>{
        if(this.state.moving){
            this.setState({...this.state, moving: false, hoveredInTask: false});
            this.unfocusInWin_Booter();
            document.removeEventListener("pointermove", this.pointerMoveCB);
            document.removeEventListener("pointerup", this.pointerUpCB);
            document.body.classList.remove("moving");
        }
    }
    pointerDownCB = (event :React.PointerEvent<HTMLDivElement>)=>{
        if(event.currentTarget === event.target && event.isPrimary){
            this.setState({...this.state, moving: true, hoveredInTask: true});
            this.focusInWin_Booter();
            document.body.classList.add("moving");
            document.addEventListener("pointermove", this.pointerMoveCB);
            document.addEventListener("pointerup", this.pointerUpCB);
            this.deltaTop = event.clientY - this.state.top;
            this.deltaLeft = event.clientX - this.state.left;
        }
    }
//#endregion
    render() :React.ReactNode{
        return(
            <div
                hidden={this.state.minimized}
                className={`${styles.window}${this.state.hoveredInTask || (this.state.active && !this.state.focusingWithIn) ? ` ${styles.hoverWindow}` : ""}${this.state.attached_t ? ` ${styles.attached_t}` : ""}${this.state.attached_b ? ` ${styles.attached_b}` : ""}${this.state.attached_l ? ` ${styles.attached_l}` : ""}${this.state.attached_r ? ` ${styles.attached_r}` : ""}`}
                style={{
                    top: `${this.state.top}px`,
                    left: `${this.state.left}px`,
                    cursor: this.state.moving ? "grabbing" : "grab"
                }}
                onPointerDown={this.pointerDownCB}
                onPointerEnter={()=>this.setState({...this.state, active: true})}
                onPointerLeave={()=>this.setState({...this.state, active: false})}
                onFocus={this.focusInWin_Booter}
                onBlur={()=>{if(!this.state.moving){this.unfocusInWin_Booter();}}}
                ref={this.outerDivRef}>
                <div
                    className={styles.fwinner}
                    onPointerEnter={()=>this.setState({...this.state, focusingWithIn: true})}
                    onPointerLeave={()=>this.setState({...this.state, focusingWithIn: false})}>
                    {/*todo:*/}<div>ControlPanel测试</div>
                    {this.props.children}
                </div>
            </div>
        );
    }
}